}
table.template_overrides_list {
  border-collapse: collapse;
  background-color: #fff;
}
table.template_overrides_list td,
table.template_overrides_list th {
  border: 1px solid #b1b6d1;
  padding: 10px;
}
table.template_overrides_list td {
  word-break: break-all;
}
