                          class="icon-q-mark"
                        ></a>
                      </div>

                      <span
                        class="status"
                        :class="{
                          enabled: googlemeet_meeting_is_account_connected,
                          disabled: !googlemeet_meeting_is_account_connected
                        }"
                        v-if="googlemeet_meeting_is_account_connected != null"
                        >{{
                          googlemeet_meeting_is_account_connected
                            ? T__("Connected")
                            : T__("Disconnected")
                        }}</span
                      >
                    </div>
                    <div class="txt-help" style="margin-bottom: 5px;">
                      {{ googlemeet_meeting_account_email }}
                    </div>
                    <div class="txt-help">
                      <span
                        v-if="googlemeet_meeting_is_account_connected === true"
                      >
                        {{
                          T__(
                            "As long as a Google Calendar account is connected, this cannot be disconnected. If you do not want to use Google Meet for your bookings, you can simply not add it as a location in your event types."
                          )
                        }}
                      </span>
                      <span
                        v-if="googlemeet_meeting_is_account_connected === false"
                        v-html="
                          Tsprintf(
                            /* translators: 1: html-tag-open 2: html-tag-close */
                            T__(
                              'This will be connected when you connect a calendar account (%1$s Settings › Calendars %2$s), and set a &quot;Add Bookings to...&quot; calendar. %3$s'
                            ),
                            '<a href=&quot;admin.php?page=wpcal_admin#/settings/calendars&quot;>',
                            '</a>',
                            '<a href=&quot;https://help.wpcal.io/en/article/how-do-i-connect-my-google-meet-with-wpcalio-hrh77h/?utm_source=wpcal_plugin&utm_medium=contextual&quot;>Learn more</a>'
                          )
                        "
                      >
                      </span>
                    </div>
                  </div>
                  <div
                    class="set-locations-single"
                    v-for="(tp_account, index) in tp_accounts"
                    :key="index"
                    :class="{
                      ['location_' + tp_account.provider]: true,
                      'del-alert': show_delete_highlight(
                        'disconnect_highlight_tp_account_index',
                        index
                      )
                    }"
                  >
                    <div class="txt-h5">
                      <div class="app-name">
                        {{ tp_account.provider | tp_provider_name }}
                        <a
                          v-if="help_links[tp_account.provider]"
                          :href="help_links[tp_account.provider]"
                          target="_blank"
                          class="icon-q-mark"
                        ></a>
                      </div>
                      <div class="account-connection-actions">
                        <OverflowMenu
                          :menu_id="tp_account.id"
                          v-model="tp_account_overflow_menu_details"
                        >
                          <a
                            class="menu-item del-hl"
                            style="color:#e84653; font-size:12px;"
                            @click="
                              tp_account_overflow_menu_details.open_menu_id = null;
                              show_disconnect_confirmation(tp_account);
                            "
                            @mouseover="
                              set_mouseover_on_delete(
                                'disconnect_highlight_tp_account_index',
                                index,
                                true
                              )
                            "
                            @mouseleave="
                              set_mouseover_on_delete(
                                'disconnect_highlight_tp_account_index',
                                index,
                                false
                              )
                            "
                            >{{ T__("Disconnect") }}</a
                          >
                        </OverflowMenu>
                        <span
                          class="status "
                          :class="{
                            enabled: tp_account.status == '1',
                            disabled: tp_account.status != '1'
                          }"
                          >{{
                            tp_account.status == "1"
                              ? T__("Connected")
                              : T__("Disconnected")
                          }}</span
                        >
                      </div>
                    </div>
                    <div class="txt-help">
                      {{ tp_account.tp_account_email }}
                    </div>
                  </div>
                </div>
                <button
                  class="wpc-btn secondary md full-width"
                  @click="show_add_tp_account_list = true"
                  v-if="!show_add_tp_account_list"
                  :disabled="!enable_add_new_tp_account"
                  :title="
                    !enable_add_new_tp_account
                      ? this.T__('You have added all available apps.')
                      : ''
                  "
                >
                  {{ T__("+ Add an App") }}
                </button>
                <div v-if="show_add_tp_account_list">
                  <div class="txt-h5 caps">
                    {{ T__("Select your web conferencing app") }}
                  </div>
                  <ul class="selector block compact location">
                    <li
                      v-for="(tp_account_add_item,
                      index2) in tp_accounts_add_list_final"
                      :key="index2"
                    >
                      <label
                        @click="add_tp_account(tp_account_add_item.provider)"
                        :class="'location_' + tp_account_add_item.provider"
                        ><div class="txt-h5">
                          {{ tp_account_add_item.provider | tp_provider_name }}
                          <a
                            v-if="help_links[tp_account_add_item.provider]"
                            :href="help_links[tp_account_add_item.provider]"
                            target="_blank"
                            class="icon-q-mark"
                            @click.stop=""
                          ></a>
                        </div>
                        <div class="txt-help" style="padding-top:0;">
                          {{ T__("Web conferencing") }}
                        </div></label
                      >
                      <!-- <label
                        v-if="tp_account_add_item.provider == 'zoom_meeting'"
                        :class="'location_' + tp_account_add_item.provider"
                        ><div class="txt-h5">
                          Zoom <span class="tag">Coming soon</span
