  border: 1px solid #d8dbe7;
  display: block;
  padding: 5px;
  border-radius: 5px;
  padding-left: 30px;
  position: relative;
  margin: 5px 0;
}
.covid-consent-cont label:hover {
  background-color: #e4e9f5;
}
.covid-consent-cont label.consent-agreement input[type="checkbox"] {
  position: absolute;
  margin: 1px 0 0 -24px;
}
.covid-consent-cont button {
  padding: 13px;
  font-size: 16px;
  background-color: #567bf3;
  color: #fff;
  border: 0;
  border-radius: 5px;
  outline: 0;
  cursor: pointer;
  width: 300px;
}
.covid-consent-cont button:disabled {
  background-color: #c5c5c5;
  color: #d6d6d6;
  cursor: not-allowed;
}
</style>
