              private_event_disabled: is_disable_manage_private
            }"
          >
            {{ T__("Will this event type be privately managed?") }}
            <span
              @click="may_show_manage_private_not_changeable_error"
              style="margin-left:auto;"
            >
              <OnOffSlider
                :name="'service_private'"
                v-model="form.is_manage_private"
                true_value="1"
                false_value="0"
                style="margin-left:auto;"
                :disabled="is_disable_manage_private"
            /></span>
          </div>
