          </div>
        </form-row>

        <form-row :validator="$v.form.service_admin_user_ids">
          <label>
            {{ T__("Host") }}
            <abbr class="required" title="required">*</abbr>
            <em>{{ T__("required") }}</em>
          </label>
          <span class="txt-help">
            {{
              T__(
                "This event type will be assigned to the admin you select below. The same admin's profile will be used while displaying the event type."
              )
            }}
          </span>
          <div
            class="et-create-host-selector"
            style="margin-bottom:5px;"
            @click="may_show_host_selector_not_changeable_error"
          >
            <HostSelector1ToN
              v-model="form.service_admin_user_ids"
              :max_host="1"
              :disabled="is_disable_host_selector"
            />
          </div>
          <div v-if="is_disable_host_selector">
            {{
              T__(
                "You cannot change the host for your privately managed event types."
              )
            }}
          </div>
          <div style="margin-top: 5px;">
            <router-link to="/settings/admins"
              >Add new host to WPCal</router-link
            >
          </div>
        </form-row>

        <div class="wpc-form-row" v-if="view == 'add'">
          <label for="form_unique_link" style="cursor:default;">
            {{ T__("Event type booking page") }}
          </label>
          <span class="txt-help">
            {{
              T__(
                "A page will be created and the booking widget will be added to it. You can manage it after creating this event type."
              )
            }}
          </span>
        </div>

        <!-- <form-row :validator="$v.form.locations">
          <label for="form_locations">
            Location
            <em>optional</em>
