            This page will be created and the booking widget will be displayed
            here.
          </span>
        </div> -->
        <form-row :validator="$v.form.color">
          <label>
            {{ T__("Event type color") }}
            <em>{{ T__("required") }}</em>
          </label>
          <ColorSelector :form="form" color_prop="color" />
        </form-row>

        <!-- {{ $v.step2 }} -->
        <div class="wpc-form-row">
          <button
            v-if="view == 'add'"
            type="button"
            class="wpc-btn primary lg"
            @click="$emit('validate-may-navigate-or-submit', 2)"
