<script>
import ColorSelector from "@/components/form_elements/ColorSelector.vue";
import AdminLocationSelector from "@/components/form_elements/AdminLocationSelector.vue";
import HostSelector1ToN from "@/components/form_elements/HostSelector1ToN.vue";
import OnOffSlider from "@/components/form_elements/OnOffSlider.vue";

import { isEqual as _isEqual } from "lodash-es";

export default {
  name: "ServiceFormStep2",
  components: {
    ColorSelector,
    AdminLocationSelector,
    HostSelector1ToN,
    OnOffSlider
  },
  props: {
    view: {
      type: String
    },
    step: {
      type: Object,
      required: true
    },
    form: {
      type: Object,
      required: true
    },
    saved_form: {
      type: Object
    },
    create_or_update_btn_is_enabled: {},
    $v: {}
  },
  data() {
    return {};
  },
  computed: {
    is_disable_manage_private() {
      return (
        this.form.is_manage_private == "0" && !this.is_current_admin_is_host
      );
    },
    is_current_admin_is_host() {
      return (
        this.form.service_admin_user_ids.indexOf(
          this.$store.getters.get_current_admin_details.id
        ) != -1
      );
    },
    is_disable_host_selector() {
      return this.is_current_admin_is_host && this.form.is_manage_private == 1;
    }
  },
  methods: {
    auto_focus_form_element() {
      if (this.view == "add" && !this.form.name && this.$refs.service_name) {
        setTimeout(() => {
          this.$refs.service_name.focus();
        }, 50);
      }
    },
    may_show_host_selector_not_changeable_error() {
      if (!this.is_disable_host_selector) {
        return;
      }
      this.$modal.show("dialog", {
        title: this.T__("Heads up!"),
        text: this.T__(
          "You cannot change the host for your privately managed event types."
        ),
        buttons: [
          {
            title: this.T__("Okay"),
            default: true // Will be triggered by default if 'Enter' pressed.
          }
        ]
      });
    },
    may_show_manage_private_not_changeable_error() {
      if (!this.is_disable_manage_private) {
        return;
      }
      this.$modal.show("dialog", {
        title: this.T__("Heads up!"),
        text: this.T__(
          "You cannot set another admin's event type as privately managed."
        ),
        buttons: [
          {
            title: this.T__("Okay"),
            default: true // Will be triggered by default if 'Enter' pressed.
          }
        ]
      });
    },
    may_show_on_change_host_selector_info() {
      console.log("may_show_on_change_host_selector_info");
      if (
        this.view != "edit" ||
        _isEqual(
          this.saved_form.service_admin_user_ids,
          this.form.service_admin_user_ids
        )
      ) {
        return;
      }
      let dialog_descr =
        "<ul class='bull'><li>" +
        this.T__("Existing bookings will NOT be affected.") +
        "</li><li>" +
        this.T__(
          "For existing bookings, the host will not be changed. So, the old host's email, calendar apps and meeting apps will be used."
        ) +
        "</li><li>" +
        this.T__(
          "New and rescheduled bookings will be assigned to the new host. So, emails related to it will be sent to the new host. Also, the new host's email, calendar apps and meeting apps will be used."
        ) +
        "</li></ul>";

      this.$modal.show("dialog", {
        title: this.T__("Change the host?"),
        text: this.Tsprintf(
          this.T__("Here's what will happen -" + dialog_descr)
        ),
        buttons: [
          {
            title: this.T__("Yes, change the host.")
          },
          {
            title: this.T__("No, don't."),
            handler: () => {
              this.form.service_admin_user_ids = this.clone_deep(
                this.saved_form.service_admin_user_ids
              );
              this.$modal.hide("dialog");
            }
          }
        ]
      });
    }
  },
  watch: {
    "step.form": function(new_value) {
      if (new_value) {
        this.auto_focus_form_element();
      }
    },
    "form.service_admin_user_ids": function() {
      this.may_show_on_change_host_selector_info();
    }
  },
  mounted() {
    if (this.view == "add") {
      this.$props.step.summary = true;
    }
  }
};
</script>
