                        />
                        <!-- 
                          slot-scope="{ inputProps, inputEvents, hidePopover }"
                          @blur="
                            //log('at_blur_from');
                            $v.form.default_availability_details.from_date.$touch();
                            hidePopover();
                            ref_click(
