                    value="units"
                    v-model="form.min_schedule_notice.type"
                  />
                  {{ T__("Prevent booking of slots less than") }}
                  <div style="padding-left: 22px; padding-top: 4px;">
                    <input
                      type="number"
                      style="width: 78px; padding: 3px 5px; margin-top: -8px; font-size: 13px;"
                      v-model="form.min_schedule_notice.time_units"
                      @focus="form.min_schedule_notice.type = 'units'"
                      @blur="$v.form.min_schedule_notice.time_units.$touch()"
                    />
                    <select
                      style="width: auto; margin-top: -3px;"
                      v-model="form.min_schedule_notice.time_units_in"
                      @focus="form.min_schedule_notice.type = 'units'"
                    >
                      <option value="mins">{{ T__("mins") }}</option>
                      <option value="hrs" selected>{{ T__("hrs") }}</option>
                      <option value="days">{{ T__("days") }}</option>
                    </select>
                    {{
                      T_x("away.", "Prevent booking of slots less than _ away")
                    }}
                  </div>
                  <form-row
                    :validator="$v.form.min_schedule_notice.time_units"
                  ></form-row>
                </label>
                <label style="font-size: 13px; margin-bottom: 1px;"
                  ><input
