                    value="time_days_before"
                    v-model="form.min_schedule_notice.type"
                  />
                  {{ T__("Prevent all bookings after") }}
                </label>
                <!-- <input
                    type="text"
                    placeholder="hh:mm am"
                    style="width: 78px;
    padding: 3px 5px;
    margin-top: -2px;
    font-size: 13px;"
                    v-model="form.min_schedule_notice.days_before_time"
                    @focus="form.min_schedule_notice.type = 'time_days_before'"
                    @blur="
                      $v.form.min_schedule_notice.days_before_time.$touch()
                    "
                  /> -->
                <div
                  style="display:flex; align-items: center;"
