                      form.min_schedule_notice.type = 'time_days_before'
                    "
                  /><span style="margin-left: 3px;"
                    ><span v-if="time_format == 'HH:mm'">hrs</span>,</span
                  >

                  <select
                    style="width: auto; margin-left: 5px;"
                    v-model="form.min_schedule_notice.days_before"
                    @focus="form.min_schedule_notice.type = 'time_days_before'"
                  >
                    <option
                      v-for="(val, index) in [0, 1, 2, 3, 4, 5, 6, 7]"
                      :key="index"
                      :value="val"
                      >{{ val }}</option
                    >
                  </select>
                  <label
                    for="min_schedule_notice_type_time_days_before"
