                  T__(
                    "Set aside preparation, rest or travel time before or after events. For example, if you define a 5-minute buffer before your events, we will make sure you have 5 minutes of free time before your scheduled events."
                  )
                }}
              </div>
              <div class="col-main cf">
                <div class="col50">
