                    {{ T__("BEFORE EVENT") }}
                  </div>
                  <select
                    v-model="form.event_buffer_before"
                    @change="$v.form.event_buffer_before.$touch()"
                  >
                    <option
                      :value="option.value"
                      v-for="(option,
                      index) in event_buffer_before_element_setting.options"
                      :key="index"
                      >{{ option.text }}</option
                    >
                  </select>
                </div>
                <div class="col50">
                  <div class="label txt-help" style="padding-bottom:5px;">
                    {{ T__("AFTER EVENT") }}
                  </div>
                  <select
                    v-model="form.event_buffer_after"
                    @change="$v.form.event_buffer_after.$touch()"
                    >>
                    <option
                      :value="option.value"
                      v-for="(option,
                      index) in event_buffer_after_element_setting.options"
                      :key="index"
                      >{{ option.text }}</option
                    >
                  </select>
                </div>
              </div>
              <form-row :validator="$v.form.event_buffer_before"></form-row>
              <form-row :validator="$v.form.event_buffer_after"></form-row>
            </li>
          </ul>
        </div>

        <!-- <pre
          style="resize: both; overflow: auto; border: 2px solid; padding: 20px;   width: 300px;height:50px;"
          >{{ $v.step3 }}</pre
        > -->
        <div class="wpc-form-row">
          <button
            style="margin: 10px 0;"
            v-if="view == 'add'"
            type="button"
            class="wpc-btn primary lg"
            :disabled="!create_or_update_btn_is_enabled"
            @click="$emit('validate-may-navigate-or-submit', 3)"
          >
            {{ T__("Create Event Type") }}
          </button>
          <button
            style="margin: 10px 0;"
            v-else-if="view == 'edit'"
            type="button"
            class="wpc-btn primary lg"
            :disabled="!create_or_update_btn_is_enabled"
            @click="$emit('submit-form', 3)"
          >
            {{ T__("Save") }}
          </button>
          <button
            v-if="view == 'edit'"
            type="button"
            @click="$emit('edit-view-toggle-steps')"
            class="wpc-btn tert-link sm mt10 center"
          >
            {{ T__("Cancel") }}
          </button>
          <div
