              T__(
                "You can configure your event type further after creating it."
              )
            }}
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import InlineRadioSelector from "@/components/form_elements/InlineRadioSelector.vue";
import TimeRange1ToN from "@/components/form_elements/TimeRange1ToN.vue";
import WeekDaysSelector from "@/components/form_elements/WeekDaysSelector.vue";
import TimeZoneSelector from "@/components/form_elements/TimeZoneSelector.vue";
import TimePicker from "@/components/form_elements/TimePicker.vue";

import { debounce } from "lodash-es";

export default {
  name: "ServiceFormStep3",
  props: {
    view: {
      type: String
    },
    step: {
      type: Object,
      required: true
    },
    form: {
      type: Object,
      required: true
    },
    create_or_update_btn_is_enabled: {},
    $v: {}
  },
  components: {
    InlineRadioSelector,
    TimeRange1ToN,
    WeekDaysSelector,
    TimeZoneSelector,
    TimePicker
  },
  data() {
    return {
      show_advance_options: false,
      event_duration_element_setting: {
        name: "duration",
        options: [
          { value: 15, text: 15 },
          { value: 30, text: 30 },
          { value: 45, text: 45 },
          { value: 60, text: 60 }
        ],
        custom_option: true,
        style_class: []
      },
      display_start_time_every_element_setting: {
        name: "display_start_time_every",
        options: [
          { value: 15, text: 15 },
          { value: 30, text: 30 },
          { value: 45, text: 45 },
          { value: 60, text: 60 }
        ],
        custom_option: true,
        style_class: [] //"avail-increments"
      },
      event_buffer_before_element_setting: {
        name: "event_buffer_before",
        options: [
          { value: 0, text: "0 " + this.T_x("min", "mintue") },
          { value: 5, text: "5 " + this.T_x("min", "mintue") },
          { value: 10, text: "10 " + this.T_x("min", "mintue") },
          { value: 15, text: "15 " + this.T_x("min", "mintue") },
          { value: 30, text: "30 " + this.T_x("min", "mintue") },
          { value: 45, text: "45 " + this.T_x("min", "mintue") },
          { value: 60, text: "1 " + this.T_x("hr", "hour") },
          {
            value: 90,
            text:
              "1 " +
              this.T_x("hr", "hour") + // intentional line break, i18n search replace
              " 30 " +
              this.T_x("min", "mintue")
          },
          { value: 120, text: "2 " + this.T_x("hr", "hour") },
          { value: 150, text: "2 hr 30 " + this.T_x("min", "mintue") },
          { value: 180, text: "3 " + this.T_x("hr", "hour") }
        ],
        selected_value: 30
      },
      event_buffer_after_element_setting: {
        name: "event_buffer_after",
        options: [
          { value: 0, text: "0 " + this.T_x("min", "mintue") },
          { value: 5, text: "5 " + this.T_x("min", "mintue") },
          { value: 10, text: "10 " + this.T_x("min", "mintue") },
          { value: 15, text: "15 " + this.T_x("min", "mintue") },
          { value: 30, text: "30 " + this.T_x("min", "mintue") },
          { value: 45, text: "45 " + this.T_x("min", "mintue") },
          { value: 60, text: "1 " + this.T_x("hr", "hour") },
          {
            value: 90,
            text:
              "1 " +
              this.T_x("hr", "hour") + // intentional line break, i18n search replace
              " 30 " +
              this.T_x("min", "mintue")
          },
          { value: 120, text: "2 " + this.T_x("hr", "hour") },
          {
            value: 150,
            text:
              "2 " +
              this.T_x("hr", "hour") + // intentional line break, i18n search replace
              " 30 " +
              this.T_x("min", "mintue")
          },
          { value: 180, text: "3 " + this.T_x("hr", "hour") }
        ],
        selected_value: 30
      }
    };
  },
  computed: {
    show_advance_options_txt() {
      return this.show_advance_options
        ? this.T__("- Hide Advanced options")
        : this.T__("+ Show Advanced options");
    },
    show_relative_date_range: {
      // getter
      get: function() {
        let misc_value = this.form.default_availability_details.date_misc;
        if (
          this.form.default_availability_details.date_range_type ===
            "relative" &&
          misc_value
        ) {
          let show_misc_value = misc_value.replace(/^\+((\d)*)D$/gm, function(
            x,
            y
          ) {
            return y;
          });
          return show_misc_value;
        }
        return "";
      },
      // setter
      set: function(new_value) {
        new_value = new_value.trim();
        if (new_value.length > 0) {
          this.form.default_availability_details.date_misc =
            "+" + new_value + "D";
        } else {
          this.form.default_availability_details.date_misc = "";
        }
      }
    },
    default_availability_details_from_date_obj: {
      get: function() {
        if (this.default_availability_details_from_date) {
          let parsed_date = this.date_parse(
            this.default_availability_details_from_date
          );
          return parsed_date;
        }
        return "";
      },
      set: function(new_value) {
        if (
          this.form.default_availability_details.date_range_type == "from_to"
        ) {
          this.form.default_availability_details.from_date = this.$options.filters.wpcal_format_std_date(
            new_value
          );
        }
      }
    },
    default_availability_details_to_date_obj: {
      get: function() {
        if (this.default_availability_details_to_date) {
          let parsed_date = this.date_parse(
            this.default_availability_details_to_date
          );
          return parsed_date;
        }
        return "";
      },
      set: function(new_value) {
        if (
          this.form.default_availability_details.date_range_type == "from_to"
        ) {
          this.form.default_availability_details.to_date = this.$options.filters.wpcal_format_std_date(
            new_value
          );
        }
      }
    },
    default_availability_details_from_date: {
      get: function() {
        return this.form.default_availability_details.date_range_type ==
          "from_to"
          ? this.form.default_availability_details.from_date
          : "";
      },
      set: debounce(function(new_value) {
        this.form.default_availability_details.from_date = new_value;
      }, 1500)
    },
    default_availability_details_to_date: {
      get: function() {
        return this.form.default_availability_details.date_range_type ==
          "from_to"
          ? this.form.default_availability_details.to_date
          : "";
      },
      set: debounce(function(new_value) {
        this.form.default_availability_details.to_date = new_value;
      }, 1500)
    }
  },
  mounted() {
    this.$root.$on("show-advance-options", () => {
      this.show_advance_options = true;
    });
  }
};
</script>

<style lang="css" scoped>
.custom-radio-cont label {
  display: block;
  margin-bottom: 10px;
}
.custom-radio-cont label:last-child {
  margin-bottom: 0;
}
